//***FONTS***//

$f-montserrat: "Montserrat", sans-serif;
$f-roboto: "Roboto", sans-serif;

//***ICONS***//
$font-family-icon: "icomoon" !default;
$font-path-icon: "../fonts/icons" !default;

$icon-user: "\f007";
$icon-bar-chart: "\f080";
$icon-bar-chart-o: "\f080";
$icon-envelope: "\f0e0";
$icon-address-book: "\f2b9";
$icon-id-badge: "\f2c1";
$icon-linkedin: "\e900";
$icon-github: "\e901";
$icon-twitter: "\e902";
$icon-facebook: "\e903";
$icon-facebook-f: "\e903";
$icon-home: "\e904";
$icon-chartpoll: "\e908";
$icon-sliders: "\e90a";
$icon-facebook1: "\ea90";
$icon-twitter1: "\ea96";
$icon-github1: "\eab0";
$icon-linkedin2: "\eaca";

@font-face {
  font-family: "#{$font-family-icon}";
  src: url("#{$font-path-icon}/#{$font-family-icon}.eot?iv3zmf");
  src: url("#{$font-path-icon}/#{$font-family-icon}.eot?iv3zmf#iefix")
      format("embedded-opentype"),
    url("#{$font-path-icon}/#{$font-family-icon}.ttf?iv3zmf") format("truetype"),
    url("#{$font-path-icon}/#{$font-family-icon}.woff?iv3zmf") format("woff"),
    url("#{$font-path-icon}/#{$font-family-icon}.svg?iv3zmf##{$font-family-icon}")
      format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

/*alternative selector*/
// [class^='icon-'],
// [class*=' icon-']

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "#{$font-family-icon}" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-user {
  &:before {
    content: $icon-user;
  }
