.btn {
  text-decoration: none;
  position: relative;
  font-size: 10px;
  font-size: calc(10px + 1vmin);
  line-height: 1.5;
  display: block;
  margin: 0 auto;
  white-space: nowrap;
  padding: calc(0.2vw + 0.8rem) calc(1vw + 2.5rem);
  text-align: center;
  color: #fffbeb;
  box-sizing: border-box;
  border: 1px solid #2cac8c;
  transition: border 0.2s linear;
  backface-visibility: hidden;
}

.btn:active {
  border-color: rgba(44, 172, 140, 0.5);
}

.btn::before, .btn::after {
  content: '';
  position: absolute;
  box-sizing: inherit;
  background: transparent;
  transform-origin: center center;
  transition: all 0.2s ease-in;
}

.btn::before {
  left: 5px;
  right: 5px;
  top: -1px;
  bottom: -1px;
}

.btn::after {
  top: 5px;
  bottom: 5px;
  left: -1px;
  right: -1px;
}

.btn::before {
  border-top: 2px solid #404040;
  border-bottom: 2px solid #404040;
  transform: scale3d(1, 1, 1);
}

.btn:hover::before {
  transform: scale3d(0, 1, 1);
}

.btn:active::before {
  transform: scale3d(0, 1, 1);
}

.btn::after {
  border-left: 2px solid #404040;
  border-right: 2px solid #404040;
  transform: scale3d(1, 1, 1);
}

.btn:hover::after {
  transform: scale3d(1, 0, 1);
}

.btn:active::after {
  transform: scale3d(1, 0, 1);
}

