.burger--mobile {
  margin-left: auto;
  display: flex;
  position: fixed;
  z-index: 2500 !important;
  top: 2rem;
  right: 2rem;
  margin-right: 0;
  /*active-state*/
  /*toggle menu - active-state*/
  /*bars*/
}

.burger--mobile .burger-bars {
  position: relative;
  top: 0;
  left: 0;
  width: 3rem;
  height: 3rem;
  text-decoration: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  outline: 0;
  mix-blend-mode: difference;
}

.burger--mobile .burger-bar {
  width: 100%;
  height: 0.2rem;
  position: relative;
  background-color: rgba(209, 206, 206, 0.8);
  transition: all 0.2s linear;
}

.burger--mobile .burger-bar::before,
.burger--mobile .burger-bar::after {
  content: '';
  position: absolute;
  height: 0.2rem;
  background-color: inherit;
}

.burger--mobile .burger-bar::before {
  transform: translateY(-0.8rem);
  width: 100%;
  transition: rotate 0.25s ease-in;
}

.burger--mobile .burger-bar::after {
  transform: translateY(0.8rem);
  width: 75%;
  transition: rotate 0.4s ease-in-out;
}

.burger--mobile .burger-bars.inactive span {
  transform: rotate(45deg);
  width: 83%;
  background-color: rgba(17, 17, 17, 0.8);
}

.burger--mobile .burger-bars.inactive span::before {
  width: 40%;
  top: -0.8rem;
  transform: rotate(-90deg);
  right: 0.8rem;
}

.burger--mobile .burger-bars.inactive span::after {
  right: 0.8rem;
  width: 40%;
  top: 0.8rem;
  transform: rotate(90deg);
}

.burger {
  position: fixed;
  z-index: 1500;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  transition: transform 0.1s ease-in, opacity 0.2s ease-out;
  padding: 5px 0;
  margin-left: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: space-between;
  cursor: pointer;
  mix-blend-mode: difference;
}

.burger.active {
  opacity: 1;
  visibility: visible;
  transition: all 0.5s ease-out;
}

.burger.inactive {
  visibility: hidden;
  opacity: 0;
}

.burger:hover .burger__bar:nth-child(1) {
  width: 3.5rem;
}

.burger:hover .burger__bar:nth-child(2) {
  width: 5rem;
}

.burger:hover .burger__bar:nth-child(3) {
  width: 2.8rem;
}

.burger:hover .burger__txt > span:nth-child(odd) {
  bottom: 3px;
}

.burger:hover .burger__txt > span:nth-child(2) {
  bottom: -6px;
}

.burger:hover .burger__txt > span:nth-child(4) {
  bottom: -15px;
}

.burger__txt {
  position: relative;
  margin-left: -0.5rem;
  display: flex;
  transform: rotateZ(-90deg);
  text-align: start;
  font-family: "Roboto", sans-serif;
  color: #fffbeb;
  font-size: 1.2rem;
  line-height: 1;
  font-weight: 500;
  text-transform: uppercase;
}

.burger__txt span {
  position: relative;
  display: block;
  bottom: 0;
  padding: 2px;
  font-size: inherit;
  transition: 0.3s all ease-in-out;
}

.burger__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 3rem;
  height: 1.9rem;
