  }
  15% {
    opacity: 1;
    box-shadow: 0 1rem 1rem rgba(55, 128, 109, 0.1);
    transform: translate3d(200%, calc(400% - 8 * 50% - (8 + 1) * 10px), 0) scale3d(1.1, 0.9, 1) perspective(100px) rotateX(50deg);
  }
  25% {
    opacity: 1;
    box-shadow: 0 1rem 1rem rgba(55, 128, 109, 0.1);
    transform: translate3d(200%, calc(400% - 8 * 50% - (8 + 1) * 10px), 0) scale3d(1.1, 0.9, 1) perspective(100px) rotateX(50deg);
