.contact {
  width: 100%;
  min-height: 300px;
  height: calc(35vh + 1vw);
  margin: 0 auto;
  border-top: solid 1px;
  border-image: linear-gradient(45deg, #2cac8c, rgba(173, 48, 37, 0.8)) 1;
  color: #fffbeb;
  font-family: "Montserrat", sans-serif;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 896px) and (orientation: landscape) and (max-height: 460px) {
  .contact {
    height: 80vh;
  }
}

.contact__inner {
  overflow: hidden;
  height: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 0 3rem;
}

@media only screen and (min-width: 768px) {
  .contact__inner {
    padding: 0 8rem;
  }
}

.contact__title-container {
  height: auto;
  width: 100%;
  flex-basis: 10%;
  display: none;
  align-items: center;
  justify-content: center;
}

.contact__title {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact__title-txt {
  line-height: 1.5;
  white-space: nowrap;
  font-size: 28px;
}

@media screen and (max-width: 420px) {
  .contact__title-txt {
    font-size: 7vmin;
  }
}

@media (min-width: 420px) {
  .contact__title-txt {
    font-size: calc(0.57471vw + 25.58621px);
  }
}

@media (min-width: 768px) {
  .contact__title-txt {
    font-size: calc(0.89286vw + 23.14286px);
  }
}

@media (min-width: 992px) {
  .contact__title-txt {
    font-size: calc(1.1194vw + 20.89552px);
  }
}

@media (min-width: 1260px) {
  .contact__title-txt {
    font-size: calc(4500vw - 56665px);
  }
}

@media (min-width: 1261px) {
  .contact__title-txt {
    font-size: calc(4.42478vw + 24.20354px);
  }
}

@media (min-width: 1600px) {
  .contact__title-txt {
    font-size: calc(6.25vw - 5px);
  }
}

@media (min-width: 1920px) {
  .contact__title-txt {
    font-size: 115px;
  }
}

@media screen and (max-width: 896px) and (orientation: landscape) and (max-height: 460px) {
  .contact__title-txt {
    font-size: 7vmin;
  }
}

@media only screen and (min-width: 1921px) {
  .contact__title-txt {
    font-size: 13rem;
  }
}

.contact__content-line {
  flex-basis: 20%;
  height: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.contact__content-line-inner {
  margin: 0 auto;
  height: auto;
  display: flex;
  justify-content: center;
  width: 100%;
}

.contact__contacts {
  height: auto;
  width: 100%;
  flex-basis: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact__contact-container {
  height: auto;
  flex-basis: 70%;
  width: calc(700px + 12vw);
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.contact__contact-item {
  height: auto;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 1.5rem;
}

.contact__item-name, .contact__item-data {
  min-width: 100%;
  width: 100%;
  flex-basis: 100%;
  padding-left: calc(50% - 10rem);
  margin: 0 auto;
  box-sizing: border-box;
}

.contact__item-name *, .contact__item-data * {
  white-space: nowrap;
}

.contact .email-data {
  overflow: hidden;
  min-height: 2.5rem;
  align-self: stretch;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  left: calc(50% - 10rem);
  cursor: pointer;
}

.contact .email-data span {
  width: 100%;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  padding: 0.5rem 0;
  transition: transform 0.2s linear;
  will-change: transform;
}

.contact .email {
  transform: translateY(-50%);
}

.contact .email-hover {
  transform: translateY(50%);
}

.contact .email-copied {
  color: #2cac8c;
  transform: translateY(150%);
}

@supports (background: -webkit-linear-gradient(red, blue)) {
  .contact .email-copied {
    color: transparent;
    background: -webkit-linear-gradient(105deg, #2cac8c, #ad3025);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

@media only screen and (min-width: 768px) {
  .contact__item-name {
    padding-left: 10%;
    min-width: 40%;
    width: 40%;
    flex-basis: 40%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .contact__item-data {
    padding-left: 0;
    min-width: 40%;
    width: 40%;
    flex-basis: 40%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .contact .email-data {
    left: 0;
  }
}

.contact__facebook, .contact__twitter {
  cursor: pointer;
  outline: 0;
  text-decoration: none;
  color: inherit;
}

.contact__facebook:active, .contact__twitter:active, .contact__facebook:visited, .contact__twitter:visited {
  text-decoration: none;
}

.contact__facebook {
  padding-right: 1rem;
  color: #3b5998;
}

.contact__twitter {
  color: #38a1f3;
}

.contact__facebook:hover, .contact__twitter:hover {
  color: #a8a8a8;
}

.contact__copyright-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  flex-basis: 10%;
}

.contact__copyright {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  padding-right: 10%;
}

.contact__copyright small {
  font-size: 8px;
  font-size: 10px;
}

@media (min-width: 320px) {
  .contact__copyright small {
    font-size: calc(2vw + 3.6px);
  }
}

@media (min-width: 420px) {
  .contact__copyright small {
    font-size: calc(0.57471vw + 9.58621px);
  }
}

@media (min-width: 768px) {
  .contact__copyright small {
    font-size: calc(0.44643vw + 10.57143px);
  }
}

@media (min-width: 992px) {
  .contact__copyright small {
    font-size: calc(0.22321vw + 12.78571px);
  }
}

@media (min-width: 1440px) {
  .contact__copyright small {
    font-size: 16px;
  }
}

@media screen and (max-width: 896px) and (orientation: landscape) and (max-height: 460px) {
  .contact__copyright small {
    font-size: 10px;
  }
}

.contact__scroll-up {
  margin-left: auto;
  display: flex;
  position: relative;
}

.contact__scroll-up-container {
  position: absolute;
  transform: translateY(-150%);
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  opacity: 1;
}

.contact__scroll-up-container a {
  text-decoration: none;
  color: inherit;
  outline: 0;
}

.contact__scroll-up-sign {
  margin-bottom: 1.2rem;
  position: relative;
  will-change: transform;
  transition: translate 1s linear;
}

.contact__scroll-up-sign svg {
  display: block;
  margin: 0 auto;
  stroke: #2cac8c;
  fill: none;
  width: 12px;
  height: 38px;
}

.contact__scroll-up-sign svg line {
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 2px;
}

.contact__scroll-up-txt {
  font-family: "Montserrat", sans-serif;
  font-size: 1.7rem;
}

@media only screen and (min-width: 768px) {
  .contact__scroll-up-txt {
    font-size: 1.8rem;
  }
}

@media only screen and (min-width: 1260px) {
  .contact__scroll-up-txt {
    font-size: 2rem;
  }
}

.contact__scroll-up-container.hidden {
  visibility: hidden;
  transition: visibility 0.1s ease-in;
}

.contact__scroll-up-container.visible {
  visibility: visible;
}

@media only screen and (min-width: 1260px) {
  .contact {
    height: 55vh;
  }
  .contact__inner {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
  }
  .contact__title-container {
    display: flex;
    position: relative;
    width: 10%;
    flex-basis: 10%;
    top: 0;
    left: 0;
  }
  .contact__title {
    align-self: flex-end;
  }
  .contact__title-txt {
    writing-mode: vertical-lr;
    text-orientation: mixed;
    font-weight: 600;
    line-height: 1;
    letter-spacing: -4px;
    opacity: 0.25;
    /* Fallback for IE */
    color: #111111;
    color: var(--mainBgColor);
    text-shadow: -1px -1px 0 #fffbeb, 1px -1px 0 #fffbeb, -1px 1px 0 #fffbeb, -1px 1px 0 #fffbeb, 1px 1px 0 #fffbeb;
  }
  @supports (-webkit-text-stroke: 1px grey) or (-moz-text-stroke: 1px grey) {
    .contact__title-txt {
      text-shadow: 0 0 0 transparent;
      -webkit-text-fill-color: transparent;
      /* Will override color (regardless of order) */
      -moz-text-fill-color: transparent;
      -webkit-text-stroke: 1px #fffbeb;
      -moz-text-stroke: 1px #fffbeb;
    }
  }
  .contact__contacts {
    width: 90%;
    flex-basis: 90%;
    align-self: stretch;
  }
  .contact__content-line {
    flex-basis: 25%;
    margin-bottom: 0;
  }
  .contact__scroll-up-container {
    transform: translate(-100%, -150%);
  }
}
