//***SCROLL-ARROW***//
.scroll-arw-box {
  position: fixed;
  z-index: 100;
  bottom: 0;
  left: 47%;
  transform: translate(-50%, -20%);
  transition: left 0.05 ease-in;

  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  visibility: hidden;
  transition: opacity 0.15s ease-in;

  span {
    cursor: default;
    color: $c-font-primary;
    font-family: $f-montserrat;
    writing-mode: vertical-lr;
    display: block;
    letter-spacing: 1px;
    font-size: 1.2rem;
    text-align: center;
    font-weight: 300;
  }
  @include mq('ulwide') {
    /*scroll-arrow*/

    transform: translate(-50%, -40%);
    span {
      font-size: 1.4rem;
    }

    .arw-dwn {
      margin-top: 1rem;
      svg {
        width: 16px;
        height: 42px;
      }
    }
  }
  .arw-dwn {
    margin-top: 1.2rem;
    position: relative;
    will-change: transform;
    transition: translate 1s linear;

    svg {
      display: block;
      margin: 0 auto;
      stroke: $c-bg-thirdly;
      fill: none;
      width: 12px;
      height: 38px;
      line {
        stroke-linecap: round;
        stroke-linejoin: round;
        stroke-width: 2px;
      }
    }
  }
}

.scroll-arw-box.hidden {
  visibility: hidden;
  transition: visibility 0.1s ease-in;
}

.scroll-arw-box.visible {
  visibility: visible;
}
//***END**SCROLL-ARROW***//

//***START**SOCIAL MEDIA FOR DESKTOP DEV***//
.media {
  color: $c-font-primary;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__item {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: space-between;
    flex-direction: column;
    text-decoration: none;
    cursor: pointer;
    overflow: hidden;
    text-align: center;
    width: 3rem;
    height: 3rem;
    margin-bottom: 1.8rem;
    transition: opacity 0.2s linear;
    @media screen and (min-width: 1921px) {
      & {
        margin-bottom: 2.2rem;
      }
    }
  }
  &__item:last-of-type {
    margin-bottom: 0;
  }

  &:hover > *:not(:hover) {
    opacity: 0.6 !important;
  }

  &__item:hover > .icon-facebook {
    color: $c-hover-fb;
  }
  &__item:hover > .icon-linkedin {
    color: $c-hover-l-in;
  }
  &__item:hover > .icon-github {
    color: $c-hover-git;
  }
  &__item:hover > .icon-twitter {
    color: $c-hover-tw;
  }

  &__icon {
    color: $c-font-primary;
    font-size: 26px;
    transition: color 0.2s ease-in;
    @media screen and (min-width: 1921px) {
      & {
        font-size: 28px;
      }
    }
  }
}
//***END**SOCIAL MEDIA***//

/*MEDIAQUERIES*/
@include mq('large', max) {
  .scroll-arw-box, //arrow-scroll
.media, //social media
.burger--desktop {
    display: none !important;
  }
}
@include mq('large', min) {
  .burger--mobile {
    display: none !important;
  }
}
