.hero {
  position: relative;
  width: 100%;
  min-height: 100vh;
  height: 100vh;

  @media screen and (orientation: landscape) and (max-width: 900px) and (max-height: 480px) {
    min-height: 140vh;
  }
  overflow: hidden;
  font-family: $f-montserrat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__banner-img {
    position: absolute;
    top: -1rem;
    left: -1rem;
    right: -1rem;
    bottom: -1rem;
    z-index: -1;
    --x: 0px;
    --y: 0px;
    display: block;
    width: 103%;

    height: auto;
    background-origin: center center;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: linear-gradient(to right, $c-start, $c-stop);
    @include mq('large') {
      background-position-y: var(--y);
      background-position-x: var(--x);
      background-image: url(../img/hero-background.jpeg);
      transition: background-position 0.15s ease;
      filter: blur(1px);
      will-change: background-position;
    }
    @include mq('ulwide') {
      background-image: url(../img/hero-background-large.jpeg);
    }
  }

  &__banner-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    background: linear-gradient(to right, $c-start, $c-stop);
    opacity: 0.8;
  }

  /**NAV-BAR**/
  &__nav-bar {
    box-sizing: border-box;
    position: absolute;
    left: 0;
    top: 2.5rem;
    width: 100%;
    padding: 0 8.33%;
    display: block;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 100;
  }
  //***LOGO***//
  &__logo {
    display: inline-block;
    position: relative;
    z-index: 100;
    cursor: pointer;
    text-decoration: none;
    width: 2.8rem;
    height: 2.8rem;
    margin-right: auto;
    margin-left: 0;
  }

  &__logo-svg {
    margin: 0 auto;
    display: block;
    width: 100%;
    height: 100%;
    fill: none;
    overflow: visible;

    &:only-child {
      stroke-width: 3px;
      stroke: $c-font-primary;
    }
  }
  //***END**LOGO***//

  //***MAIN-NAVIGATION**HERO-SECTION*//
  &__nav--desktop {
    margin-left: auto;
    margin-right: 0;
    display: flex;
    color: $c-font-primary;
    font-family: $f-montserrat;
    font-size: 2.2rem;
    align-self: stretch;
    align-items: center;
  }
  &__nav-box {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-between;
  }
  &__nav-item {
    padding: 0;
    display: block;
    margin-right: 4rem;

    &:last-of-type {
      margin-right: 0;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      text-decoration: none;
      color: inherit;
      position: relative;
      cursor: pointer;

      /*underscore*/
      &::before {
        position: absolute;
        content: '';
        top: 0.6rem;
        margin-bottom: 1px;
        left: 0;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        transform-origin: center;
        transition: scale 0.2s ease-in;
        border-bottom: 1px solid $c-bg-thirdly;
        transform: scale3d(0, 1, 1); // Shrink only width
      }

      /*hover*/
      &:hover::before {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1); // Show full-size
        transition: transform 0.4s;
      }

      &:hover .hero__nav-icon {
        margin-right: 1rem;
        transform: scale(1);
      }

      &:hover .nav-dot {
        opacity: 0;
      }
      /*active*/
      &:active::before {
        border-bottom: 1px solid $c-bg-grey-light;
      }
    }
  }

  &__icon-wrapper {
    position: relative;
    width: 2rem;
    height: 2rem;
    span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block;
      transition: opacity 0.2s ease-in;
      width: 0.2rem;
      height: 0.2rem;
      border-radius: 50%;
      background-color: $c-font-primary;
    }
  }
  &__nav-txt {
    display: inline-block;
    margin-left: 0.5rem;
  }
  &__nav-icon {
    position: absolute;
    text-align: center;
    vertical-align: middle;
    display: inline-block;
    transform: scale(0);
    color: $c-font-primary;
    font-size: 24px;
    width: 2rem;
    height: 2rem;
    transition: transform 0.3s ease-in;
  }

  //fix the proportion to other icons
  .icon-envelope {
    font-size: 20px;
  }
  .icon-id-badge {
    font-size: 22px;
  }
  .icon-chartpoll {
    font-size: 24px;
  }
  //***END**MAIN-NAVIGATION***//

  /*--------------------------------------------------------------------*/
  //***CONTENT***//
  &__content {
    width: auto;
    color: $c-font-primary;
    font-family: inherit;
    align-self: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 5%;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
  }

  //*HELLO-txt*//
  &__lg-greating {
    position: relative;
    font-size: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: flex-start;
    span {
      display: block;
      padding-left: 0.3rem;
      font-size: inherit;
      font-weight: 600;
      line-height: 1.5;
      letter-spacing: 2px;

      //make a var
      $strocke-color: rgba($c-font-primary, 0.5);
      /* Fallback for IE */
      color: $c-bg-primary; //same color as bg of parent
      text-shadow: -1px -1px 0 rgba(rgb(116, 116, 116), 1),
        1px -1px 0 rgba(rgb(116, 116, 116), 1),
        -1px 1px 0 rgba(rgb(116, 116, 116), 1),
        -1px 1px 0 rgba(rgb(116, 116, 116), 1),
        1px 1px 0 rgba(rgb(116, 116, 116), 1);

      @supports (-webkit-text-stroke: 1px $strocke-color) or
        (-moz-text-stroke: 1px $strocke-color) {
        text-shadow: 0 0 0 transparent; //getting rid of txt-shadow
        -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
        -moz-text-fill-color: transparent;
        -webkit-text-stroke: 1px $strocke-color;
        -moz-text-stroke: 1px $strocke-color;
      }
    }
  }

  &__dots {
    position: relative;
    margin-left: calc(0.5rem + 0.1vw);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-self: stretch;
    flex-direction: column;
  }

  &__dot-upp,
  &__dot-down {
    display: flex;
    height: 27.5%;
  }
  &__dot-down {
    margin-bottom: 100%;
  }

  &__dot-down span,
  &__dot-upp span {
    position: relative;

    display: block;
    max-width: 1rem;
    max-height: 1rem;
    height: calc(0.5rem + 1vw);
    width: calc(0.5rem + 1vw);

    border-radius: 50%;
    box-sizing: border-box;
    border-width: 1px;
    border-style: solid;

    @include mq('large') {
      border-width: 2px;
      max-width: 1.4rem;
      max-height: 1.4rem;
    }
  }

  //var here
  &__dot-down span {
    border-color: $c-bg-secondary;
    filter: hue-rotate(15deg);
  }
  &__dot-upp span {
    border-color: $c-bg-thirdly;
  }

  /*content*/
  &__content-inner {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    span {
      overflow: hidden;
      margin-bottom: 1rem;
    }

    h1,
    h3 {
      line-height: 1.5;
      text-align: left;
      opacity: 1;
      will-change: transform;
    }

    h1 {
      font-size: calc(22px + 1vmin);
      text-align: left;
    }

    h3 {
      font-size: calc(14px + 1vmin);
      font-weight: 300;
    }

    span.hero__name-hightlight {
      color: $c-bg-thirdly;

      filter: brightness(120%);
      text-transform: uppercase;
      font-weight: 400;
      line-height: 1px;
      letter-spacing: 1px;
    }
  }

  /*button*MORE ABOUT ME*/
  &__btn-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 6rem;
    overflow: hidden;
    position: relative;
    will-change: width;
    padding: 1px;
  }

  //***END**CONTENT***//

  //***START**SOCIAL MEDIA***//
  &__socials--desktop {
    position: absolute;
    margin-left: auto;
    z-index: 1500;
    top: 50%;
    right: 2rem;
    width: 2.3rem;
    transform: translateY(-50%);
    /*additional styles in components-file*/
  }
  //***END**SOCIAL MEDIA***//

  //*-----------------------***START**COMPONENTS***----------------------*//

  //**MEDIQUERIES**//

  /*CONTENT_FONT-SIZES*/
  &__content-inner {
    h1 {
      @include poly-fluid-sizing(
        'font-size',
        $map: (
          420px: 24px,
          768px: 28px,
          992px: 30px,
          1440px: 32px,
          1920px: 40px
        )
      );
      @media screen and (min-width: 1921px) {
        font-size: 48px;
      }
    }

    h3 {
      @include poly-fluid-sizing(
        'font-size',
        $map: (
          420px: 16px,
          768px: 20px,
          992px: 24px,
          1440px: 27px,
          1920px: 32px
        )
      );
      @media screen and (min-width: 1921px) {
        font-size: 35px;
      }
    }
  }

  &__cta {
    @include poly-fluid-sizing(
      'font-size',
      $map: (420px: 12px, 768px: 14px, 1440px: 16px, 1920px: 17px)
    );
    @media screen and (min-width: 1921px) {
      font-size: 18px;
    }
  }

  &__lg-greating {
    @include poly-fluid-sizing(
      'font-size',
      $map: (420px: 40px, 768px: 48px, 992px: 52px, 1440px: 60px, 1920px: 70px)
    );
    @media screen and (min-width: 1921px) {
      font-size: 75px;
    }
  }

  /*main-navigation*/
  &__nav {
    @include poly-fluid-sizing('font-size', $map: (124px: 21px, 1920px: 24px));
    @media screen and (min-width: 1921px) {
      font-size: 24px;
    }
  }

  @include mq('xsmall', min) {
    .hero {
      &__content-inner {
        h1 {
          white-space: nowrap; //prevents the text from wrapping
        }
      }
    }
  }

  @include mq('small') {
    .hero {
      &__content {
        padding: 0;
        margin-right: auto;
        margin-left: 25%;
        width: auto;
        align-items: flex-start;
        & > * {
          text-align: left;
          justify-content: flex-start;
        }
      }
      &__btn-wrapper > a {
        margin-left: 0;
        margin-right: auto;
        padding: 1rem calc(1vw + 3.5rem);
      }
    }
  }

  @include mq('medium', min) {
    .hero {
      &__content {
        margin-left: 20%;
      }
      &__logo {
        width: 3.5rem;
        height: 3.5rem;
      }
    }
  }

  @include mq('large', max) {
    .hero__nav--desktop //navigation
 {
      display: none !important;
    }
  }

  @media only screen and (min-width: 1921px) {
    .hero {
      /**logo**/
      &__logo {
        width: 4.2rem;
        height: 4.2rem;
      }
      /*main-navigation*/
      &__nav--desktop {
        font-size: 25px;
      }
    }
  }
}
